*,
::after,
::before {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}
:where(:root) {
  line-height: 1.15;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
    "Open Sans", "Helvetica Neue", sans-serif;
}
:where(main) {
  display: block;
}
:where(h1) {
  font-size: 2em;
  margin: 0.67em 0;
}
:where(p + p) {
  margin-top: 1rem;
}
:where(a) {
  background-color: transparent;
}
:where(abbr[title]) {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}
:where(code, kbd, samp, pre) {
  font-family: monospace, monospace;
  font-size: 1em;
}
:where(sub, sup) {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
:where(sub) {
  bottom: -0.25em;
}
:where(sup) {
  top: -0.5em;
}
:where(button, input, optgroup, select, textarea) {
  line-height: inherit;
  border: 1px solid currentColor;
}
:where(button) {
  overflow: visible;
  text-transform: none;
}
:where(button, [type="button"], [type="reset"], [type="submit"]) {
  -webkit-appearance: button;
  padding: 1px 6px;
}
:where(input) {
  overflow: visible;
}
:where(input, textarea) {
  padding: 1px;
}
:where(fieldset) {
  border: 1px solid currentColor;
  margin: 0 2px;
}
:where(legend) {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}
:where(progress) {
  display: inline-block;
  vertical-align: baseline;
}
:where(select) {
  text-transform: none;
}
:where(textarea) {
  overflow: auto;
  vertical-align: top;
}
:where([type="search"]) {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
:where([type="color"]) {
  background: inherit;
}
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.5;
}
::-webkit-file-upload-button,
::-webkit-search-decoration {
  -webkit-appearance: button;
  font: inherit;
}
::-moz-focus-inner {
  border: 0;
}
:-moz-focusring {
  outline: 1px dotted ButtonText;
}
:-moz-ui-invalid {
  box-shadow: none;
}
:where(hr) {
  box-sizing: content-box;
  height: 0;
  color: inherit;
  overflow: visible;
}
:where(dl, ol, ul) {
  margin: 1em 0;
}
:where(ol ol, ol ul, ol dl, ul ol, ul ul, ul dl, dl ol, dl ul, dl dl) {
  margin: 0;
}
:where(b, strong) {
  font-weight: bolder;
}
:where(audio, video) {
  display: inline-block;
}
:where(audio):not([controls]) {
  display: none;
  height: 0;
}
:where(img) {
  border: 0;
}
:where(svg):not(:root) {
  overflow: hidden;
}
:where(table) {
  text-indent: 0;
  border-color: inherit;
}
:where(details) {
  display: block;
}
:where(dialog) {
  background-color: inherit;
  border: solid;
  color: inherit;
  display: block;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: fit-content;
}
:where(dialog):not([open]) {
  display: none;
}
:where(summary) {
  display: list-item;
}
:where(canvas) {
  display: inline-block;
}
:where(template) {
  display: none;
}
:where([hidden]) {
  display: none;
}
